html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  background-color: var(--color-white);
  color: #ffffff;
}

img {
  max-width: 100%;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #ffffff;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1285px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.burger-menu {
  position: relative;
  display: none;
  width: 30px;
  height: 22px;
  cursor: pointer;
}

.burger-menu span {
  margin-top: 10px;
  display: block;
  width: 30px;
  height: 2px;
  background-color: #FFFFFF;
}

.burger-menu span::before {
  position: absolute;
  content: '';
  width: 30px;
  height: 2px;
  top: 0;
  background-color: #FFFFFF;
}

.burger-menu span::after {
  position: absolute;
  content: '';
  width: 30px;
  height: 2px;
  bottom: 0;
  background-color: #FFFFFF;
}

.menu-active__list {
  position: absolute;
  display: block;
  width: 50%;
  padding: 50px;
  left: 0;
  top: 155px;
  transform: translateY(-300%);
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  transition: transform 0.2s;
}

.menu-active__list li {
  margin-bottom: 20px;
}

.menu-active__list.open {
  transform: translateY(0%);
}

.header {
  position: relative;
  background-image: url("../img/bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.header__inner {
  margin-top: 147px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header__title {
  margin: 0 0 40px 0;
  max-width: 780px;
  text-align: center;
  font-weight: 800;
  font-size: 65px;
}

.header__text {
  max-width: 600px;
  text-align: center;
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  margin-bottom: 40px;
}

.header__btn {
  padding: 20px 32px;
  color: #ffffff;
  background-color: #FF7A00;
  border-radius: 60px;
  font-size: 21px;
  font-weight: 700;
  border: 2px solid #FF7A00;
  transition: background-color 0.2s;
}

.header__btn:last-child {
  border: 2px solid #FFFFFF;
  background-color: transparent;
  margin-left: 20px;
}

.header__btn--left {
  background-color: #FF5C00;
  transition: background-color 0.3s;
}

.header__btn--right {
  background-color: #FF7A00 !important;
  transition: background-color 0.3s;
  border: 2px solid #FF7A00 !important;
}

.arrow {
  margin-top: 155px;
  padding-bottom: 62px;
}

.menu {
  position: relative;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 150px;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.menu__list {
  display: flex;
  max-width: 642px;
  align-items: center;
}

.menu__item:not(:last-child) {
  margin-right: 60px;
}

.menu__link {
  font-size: 19px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.2s;
}

.menu__link:hover {
  color: white;
}

.menu--fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  animation: fixedMenu 1s;
  background-color: #031a1f;
  z-index: 100;
}

@keyframes fixedMenu {
  0% {
    padding: 0 150px;
  }
  50% {
    padding: 90px 150px;
  }
  100% {
    padding: 60px 150px;
  }
}

.contacts {
  display: flex;
  align-items: center;
}

.contacts__item:not(:last-child) {
  margin-right: 28px;
}

.contacts__link--phone {
  font-weight: 600;
  font-size: 19px;
  color: #ffffff;
}

.contacts__link {
  position: relative;
  display: inline-block;
  z-index: 2;
}

.contacts__link svg circle {
  fill: rgba(255, 255, 255, 0.17);
  transition: fill, 0.15s;
}

.contacts__link svg circle:hover {
  fill: rgba(255, 255, 255, 0.4);
}

.btn--active {
  border: 2px solid #FFFFFF !important;
  background-color: transparent !important;
}

.about-me__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 100px;
}

.about-me__title {
  font-weight: 700;
  text-align: center;
  font-size: 40px;
  color: #000000;
  margin-bottom: 50px;
  margin-top: 81px;
}

.about-me__text {
  padding: 5px;
  width: 550px;
  font-size: 16px;
  line-height: 43px;
  font-weight: 400;
  height: 412px;
  color: #000000;
  text-align: left;
}

.about-me__photo {
  max-width: 648px;
  max-height: 558px;
}

.about-me__photo img {
  width: 100%;
  height: 100%;
}

.portfolio {
  background-color: #031A1F;
  min-height: 705px;
  margin-bottom: 92px;
}

.portfolio__title {
  padding-top: 65px;
  margin-bottom: 60px;
  font-weight: 700;
  font-size: 40px;
  text-align: center;
}

.portfolio__text {
  margin: -2px 0 0 0;
  padding: 30px;
  background-color: #FFFFFF;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: #000000;
  border-radius: 0 0 30px 0;
}

.arrow__btns {
  position: relative;
  top: -25px;
  padding-bottom: 56px;
}

.swiper-container {
  max-width: 1300px;
  padding-bottom: 80px;
}

.swiper-scrollbar-drag {
  position: absolute;
  background-color: #FFFFFF;
  width: 134px !important;
  height: 10px;
  z-index: 100 !important;
  cursor: pointer;
}

.swiper-container-horizontal > .swiper-scrollbar {
  background: rgba(255, 255, 255, 0.44);
  border-radius: 36px;
  right: 0;
  left: 0;
  margin: 0 auto;
  width: 402px !important;
  height: 10px !important;
  bottom: 4px;
}

.swiper-button-next,
.swiper-button-prev {
  width: 13px;
  height: 26px;
  color: #FFFFFF;
}

.skills {
  margin-bottom: 85px;
}

.skills__title {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  color: #031A1F;
  margin-bottom: 50px;
}

.skills__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.skills__card {
  max-width: 400px;
  padding: 12px;
}

.contact-form {
  margin-bottom: 100px;
}

.contact-form__container {
  max-width: 735px;
}

.contact-form__title {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  color: #031A1F;
  margin-bottom: 50px;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #031A1F;
  border-radius: 30px;
}

.form__text {
  max-width: 345px;
  padding-top: 80px;
  font-weight: 800;
  font-size: 24px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 65px;
}

.form__input {
  padding-bottom: 17px;
  max-width: 355px;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.49);
  outline: none;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  margin-bottom: 45px;
}

.form__btn {
  margin: 0;
  margin-top: 60px;
  font-size: 21px;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 22px 48px 24px 49px;
  border: 2px solid #FFFFFF;
  border-radius: 61px;
  background-color: transparent;
  margin-bottom: 79px;
  transition: background-color 0.3s;
}

.form__btn:hover {
  background-color: #FF7A00;
  border: 2px solid #FF7A00;
}

.footer {
  background: linear-gradient(186.1deg, #01172D 4.83%, rgba(0, 37, 24, 0.88) 99.23%);
  background-image: url("../img/footer.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.footer__container {
  margin: 0 auto;
  max-width: 1300px;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 100px 0;
}

.footer__list {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.footer__contacts {
  margin-left: 72px;
}

.footer__item {
  margin-right: 40px;
}

.footer__item:nth-child(4) {
  margin-right: 45px;
}

.footer__item:last-child {
  margin-right: 0;
}

.footer__link {
  font-size: 19px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.2s;
}

.footer__link:hover {
  color: white;
}

@media (max-width: 1350px) {
  .menu__list {
    display: none;
  }
  .contacts {
    margin: 0 auto;
  }
  .burger-menu {
    display: block;
  }
}

@media (max-width: 768px) {
  .menu {
    padding: 60px 50px 60px 150px;
  }
  .burger-menu {
    margin-left: 134px;
  }
  .header__title {
    font-size: 50px;
  }
}
